@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
a {
  text-decoration: none;
  color: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


 .App{
   min-height: 100vh;
   background-color:#002244;
   padding-top: 130px;
   padding-bottom: 70px;
 }

 @media(max-width:700px) {
   .App{
     padding-top: 70px;
   }
   
 }

 .pageTitle{
   text-transform: uppercase;
   display: flex;
   justify-content: center;
   font-family: 'Tourney', cursive;
   font-size: 2.5vw;
   padding: 4px;
   border-radius: 50px;
   color: white;
   font-weight: 0;
 }

 @media (max-width: 1000px){
   .pageTitle{
     font-size: 6.4vw;
   }
 }
.header{
   width:100%;
  cursor: pointer;
  position: fixed;
  display:flex;
  justify-content: center;
  background:black;
  padding-bottom: 15px;
  box-shadow:  0px 1px 9px gray;
  z-index: 100;
}

.imgg{
  padding-top:15px;
  height:70px;
}

@media screen and (max-width:1000px){
  .imgg{
   height:50px;

  }
}
.ContentModel__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModel__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModel::-webkit-scrollbar {
  display: none;
}

.ContentModel__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModel__title {
  height: 12%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModel__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.ContentModel__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModel__landscape {
    display: none;
  }
  .ContentModel__portrait {
    display: flex;
    width: 38%;
  }
  .ContentModel {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModel__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModel__title {
    font-size: 3.5vw;
  }
  .ContentModel__description {
    font-size: 22px;
  }
}
.carouselItem {
  display: flex;
  flex-direction: column;
  object-fit: contain;
  padding: 10px;
}
.carouselItem__img {
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 0px 0px 5px black;
}
.media{
  display:flex;
  flex-direction:column;
  width:200px;
  padding:5px;
  margin: 5px 0;
  background-color:#770737;
  border-radius:12px;
  position:relative;
  font-family:"Montserrat";
}

.media:hover{
  background-color: #702963;
  color:black
}

@media(max-width:550px){
  .media{
    width:46%;
  }
}

.poster{
  border-radius:10px;
}

.title{
  width:100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
  color:white;
}

.subTitle{
  display:flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  padding-bottom: 3px;
  color:white;
}
.trending{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (max-width:1000px){
  .pageTitle{
  padding-bottom: 15px;
  padding-top: 30px;

  }
}
.search{
  display:flex;
  margin:15px 0;
}
